* {
  font-family: sans-serif;
}
.body {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Container {
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: auto;
  width: 256рх;
  text-align: center;
  border: 10px solid #101116;
  background: #101116;
  border-radius: 5px;
}
.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
}
input[type="text"] {
  height: 75px;
  width: 249px;
  background-color: #000;
  border: 0px;
  color: red;
  text-align: right;
  font-size: 25px;
}
#clear {
  grid-column: 1/3;
  grid-row: 1;
  color: #000;
}
#result {
  grid-column: 3/5;
  grid-row: 5;
}
#backspace {
  color: #000;
}
button {
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: #000;
  color: white;
  font-weight: 500;
}
input:focus,
input:active,
button:focus,
button :active {
  outline: none;
}
.highlight {
  background: #0990d3;
}
